
.ReactTable {
  .row__checkbox {
    -moz-appearance: checkbox;
    -webkit-appearance: checkbox;
    -ms-progress-appearance: checkbox; 
  }
}

.table__columns_active {
  text-align: center;
}

.table__columns_print input {
  width: 100%;
}

.table__move {
